import Vue from 'vue'
import VueScrollTo from 'vue-scrollto'
import router from './router'
import App from './App.vue'

import 'common/stylus/index.styl'
import 'assets/iconfont/iconfont.css'

Vue.config.productionTip = false

Vue.use(VueScrollTo, {
  container: "body",
  duration: 1500,
  easing: "ease-in-out",
  offset: 0,
  force: true,
  cancelable: true,
  onStart: false,
  onDone: false,
  onCancel: false,
  x: false,
  y: true
})

new Vue({
  render: h => h(App),
  router
}).$mount('#wrapper')
