import Vue from 'vue'
import Router from 'vue-router'
import Home from 'components/page/home'
import PageNotFound from 'components/page/pagenotfound'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: "*",
      component: PageNotFound
    }
  ]
})
