<template>
  <parallax :sectionHeight="100" :breakpoint="'(min-width: 50px)'"
            id="fullscreen-intro" class="home-intro">
    <div class="intro-container" ref="intro" :style="{ opacity: opacity}">
      <div class="intro-content">
        <h1><span>Hi, I'm Charles. I design & build</span><br>
          <span>
            <vue-typer
                :text='["websites.","open source projects.","web applications."]'>
            </vue-typer>
          </span>
        </h1>
        <div class="page-scroll"><a v-scroll-to="'#sub-footer'" class="button button-ghost">Yes, I'm Available For Hire</a></div>
        <div class="page-scroll learn-more"><a v-scroll-to="'#skills'">Learn more about me<br><i class="iconfont icon-xiangxiafanye icon-arrow-hint animated-arrow-1"></i><i class="iconfont icon-xiangxiafanye icon-arrow-hint animated-arrow-2"></i></a></div>
      </div>
    </div>
  </parallax>
</template>

<script type="text/ecmascript-6">
  import { VueTyper } from 'vue-typer'
  import Parallax from 'vue-parallaxy'

  export default {
    data() {
      return{
        scrollTop: '',
        height: '',
        opacity: ''
      }
    },
    mounted () {
      this.height = this.$refs.intro.offsetHeight
      window.addEventListener('scroll', () => {
        let scrollTop = document.documentElement.scrollTop ||
          document.body.scrollTop ||
          document.querySelector('.intro-container').scrollTop
        this.scrollTop = scrollTop
        this.opacity = ((this.height - this.scrollTop * 1.5) / this.height)
      }, true)
    },
    components: {
      VueTyper,
      Parallax
    }
  }
</script>

